* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul {
  list-style: none;
}

li,
.country__title {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.country-list__image {
  width: 40px;
}

.country__image {
  width: 40px;
  height: 40px;
}

span {
  font-style: 18px;
  margin-left: 10px;
}

country-info {
  display: flex;
}

.country__text {
  font-style: 36px;
  font-weight: bold;
}

.country-list {
  padding: 0;
}

.country-list__item {
  font-weight: bold;
}

.country-list__text {
  font-weight: normal;
}
